<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
        <el-form-item prop="inWarehouseNumber" class="fl">
          <el-input v-model.trim="query.inWarehouseNumber" clearable placeholder="入库单号" class="filter-item" style="width: 178px;" />
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="183"
        :show-operate="false"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <!-- <template slot-scope="scope">
          <el-button type="primary" @click="handleModify(scope.row)">详细</el-button>
          <el-button type="warning" @click="handleModify(scope.row)">交接</el-button>
        </template> -->
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />

    <!-- 新增 -->
    <!-- <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="editType"
      :title="dialogTitle"
      :type="type"
      @submit="init"
    /> -->

  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
// import EditDialog from './Dialog'
import { getDeleteApi } from '@/apis/modules/base/baseSupplier'
import { getListApi } from '@/apis/modules/in-storage/grounding'
import tableHeadSet from '@/mixins/tableHeadSet'

export const statusStr = {
  '1': '已上架',
  '2': '未上架'
}
export default {
  name: 'InStorageGrounding',
  components: {
    // EditDialog
  },
  mixins: [paginationPage, tableHeadSet],
  data() {
    return {
      title: this.$route.meta.title,
      loading: false,
      query: {
        inWarehouseNumber: ''
      },
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'inWarehouseNumber',
          label: '入库单号',
          align: 'center',
          // width: 120,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'inWarehouseDate',
          label: '入库时间',
          align: 'center',
          minWidth: '200',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialCode',
          label: '物料编码',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '物料名称',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'barcode',
          label: '条形码ID',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'purchaseDate',
          label: '批次号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialsUnit1',
          label: 'SN号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'status',
          prop: 'status',
          label: '上架状态',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: (status) => {
            return statusStr[status]
          },
          headerAlign: 'center',
          statusFormatter: (scope) => {
            const typeMap = {
              '1': 'danger',
              '2': 'info'
            }
            return scope.status ? typeMap[scope.status] : ''
          }
        },
        {
          module: 'word',
          prop: 'upShelfDate',
          label: '上架时间',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnSets: [{
        module: 'status',
        prop: 'status',
        label: '上架状态',
        align: 'center',
        minWidth: '',
        fixed: false,
        sortable: false,
        resizable: true,
        formatter: (status) => {
          return statusStr[status]
        },
        headerAlign: 'center',
        statusFormatter: (scope) => {
          const typeMap = {
            '1': 'danger',
            '2': 'info'
          }
          return scope.status ? typeMap[scope.status] : ''
        }
      }],
      dialogForm: {},
      supplierPullList: [],
      editType: 'add',
      dialogVisible: false
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `新增${this.title}信息`
      } else {
        return `编辑${this.title}信息`
      }
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init(type) {
      console.log('init method')
      this.loading = true
      const params = type ? { ...this.query, type: this.type } : {
        ...this.query,
        type: this.type,
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      // 更新分页
      this.init('clean')
    },
    // 批量删除
    handleDelete(params, item) {
      this.$confirm('此操作将永久删除选中信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        console.log({
          id: params[0],
          dataSources: item.dataSources
        })
        const ids = params || this.$refs.baseTableRef.tableSelectionList.map(i => i.id)
        getDeleteApi({
          id: ids[0],
          dataSources: item.dataSources
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: '删除成功'
            })
            this.init()
          }
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    handleModify(item) {
      // this.dialogForm = { ...item }
      // this.editType = 'modify'
      // this.dialogVisible = true
      this.$router.push({
        name: 'InStorageInfoDetail',
        params: {
          id: item.id
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
